.number{
    background-color: white;
    padding-top: 36px;
    border-bottom:1px solid black ;
    &__title{
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    color: #000;
    display: flex;
    justify-content: center;
    margin-bottom: 36px;
    }
    &__numbers{
    border-radius: 20px;
    padding: 10px 20px;
    width: 135px;
    height: 35px;
    box-shadow: 3px 3px 0 0 rgba(0, 0, 0, 0.25);
    background: #d7d7d7;
    border: none;
    }
    &__align{
    display: flex;
    justify-content: space-between;
    width: 500px;
    margin-left: 120px;
    margin-bottom: 36px;
    }
    &__div{
    display: flex;
    justify-content: center;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 12px;
    color: #000;
    margin-bottom: 36px;
    }
    &__resulta,
    &__resultat{
        margin-top: 0;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        color: #000;
    }

}