.slider {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  &__title {
    font-weight: 400;
    font-size: 16px;
    color: #000;
    margin: 36px 0px;
  }
  &__div {
    display: flex;
  }
  &__slider {
    margin: 0px 328px;
    border-radius: 20px;
    width: 150px;
    height: 150px;
    display: flex;
    overflow: hidden;
  }
  &__btn {
    cursor: pointer;
    background-color: transparent;
    border: none;
  }
}
.slider__button:disabled {
  cursor: not-allowed;
}
.slide {
  display: none;
}
.slide.active {
  display: block;
}
.pagination{
  display: flex;
  width: 174px;
  justify-content: space-between;
}
.dot {
  width: 18px;
  height: 3px;
  border-radius: 3px;
  background-color: #D9D9D9;
  transition: width 250ms ease-in-out;
}
.dot.active {
  width: 30px;
  background-color: #797979;
}